.Talent {
    background: url(../assets/img/talent.png) 0 -50px;
    cursor: pointer;
    width: 33%;
    max-width: 300px;
    overflow: hidden;
    position: relative;
}

.talent-inner {
    background: url(../assets/img/talent.png) right -50px;
}

.Talent:hover,
.Talent-active {
    background: url(../assets/img/talent.png) 0 -150px
}

.talent-inner:hover,
.Talent-active .talent-inner {
    background: url(../assets/img/talent.png) right -200px;
}

.Talent img {
    border: 3px solid transparent;
    -webkit-border-image: url(../assets/img/skill.png) 10 round;
         -o-border-image: url(../assets/img/skill.png) 10 round;
            border-image: url(../assets/img/skill.png) 10 round;
    position: absolute;
    left: 10%;
    top: 15%;
    width: 30px;
    height: 30px;
}

.Talent span {
    font-size: 8pt;
}

.Tier {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-align: center;
        align-items: center;
    background: url(../assets/img/talent.png);
    height: 50px;
    line-height: 50px;
    margin: auto;
    text-align: center;
    width: 100%;
}

.Description {
    border: 9px solid transparent;
    -webkit-border-image: url(../assets/img/skill.png) 5 repeat;
         -o-border-image: url(../assets/img/skill.png) 5 repeat;
            border-image: url(../assets/img/skill.png) 5 repeat;
    background: black;
    width: 100%;
    /* height: 100px; */
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
}

.Description img {
    position: initial;
}

.Description .Description-header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    margin-left: 20px;
}

.Description div {
    margin: 0;
    margin-left: 20px;
    margin-right: 20px;
    line-height: initial;
    font-size: 10pt;
    text-align: left;
}


