.CharSelector {
    border: 3px solid transparent;
    -webkit-border-image: url(../assets/img/skill.png) 5 round;
         -o-border-image: url(../assets/img/skill.png) 5 round;
            border-image: url(../assets/img/skill.png) 5 round;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
}

.SpecSelector {}

.Char {
    -ms-flex: auto;
        flex: auto;
}

.Char img {
    background: url(../assets/img/skill.png);
    padding: 5px;
    opacity: 0.7;
}

.Char img:hover,
.Char-active img {
    background: url(../assets/img/gold.png);
    opacity: 1;
}

.Char p {
    display: inline-block
}

.Char p:first-letter {
    text-transform: capitalize;
}


